body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{height:100vh;width:100vw}.App a{text-decoration:none}.App .sections{width:100%;height:100%;scroll-behavior:smooth;position:relative}.App .sections>*{scroll-snap-align:start;width:100vw}



.navbar{background-color:transparent;width:100%;height:65px;position:fixed;top:0;display:flex;justify-content:center;align-items:center;font-size:1.2rem;position:fixed;top:0;z-index:100}.navbar.active{background-color:white;opacity:0.9;box-shadow:1px 1px 4px #7f5463}.navbar-container{display:flex;justify-content:center;align-items:center;height:6vw;max-height:75px;max-width:1500px}.navbar-container p{font-family:"Lora";font-weight:bold;font-size:30px;color:#797979}.navbar-logo{color:#fff;margin-left:20px;justify-self:start;cursor:pointer;text-decoration:none;font-size:2rem;display:flex;align-items:center}.navbar-logo img{height:50px;width:50px}.navbar-logo p{margin-left:10px}.nav-menu{display:grid;grid-template-columns:repeat(5, auto);grid-gap:10px;list-style:none;text-align:center;width:60vw;justify-content:end;top:65px}.nav-menu .nav-item{height:65px}.nav-menu .nav-item .nav-links{color:black;display:flex;align-items:center;text-decoration:none;height:100%;font-size:20px;padding:0.5rem 1rem;font-family:"Montserrat";font-weight:normal}.nav-menu .nav-item .nav-links:hover{-webkit-text-decoration:underline #7f5463;text-decoration:underline #7f5463}.nav-menu .nav-item .nav-links.active{color:#7f5463}.fa-bars{color:#fff}.nav-links-mobile{display:none}.menu-icon{display:none}@media screen and (max-width: 750px){.NavbarItems{position:relative}.navbar{background:#242424;height:20vw;max-height:60px;width:100%;display:flex;justify-content:center;align-items:center;font-size:15vw;position:fixed;top:0;z-index:999}.logoimg{position:relative;height:auto;width:auto;display:block;max-height:50px;top:0;left:0;-webkit-transform:translate(-300%, 0%);transform:translate(-300%, 0%)}.nav-menu{height:calc(100vh - min(60px, 20vw));width:300px;background-color:#737373;position:fixed;right:-200%;z-index:1;display:flex;flex-direction:column;align-items:center;opacity:1;transition:all 1s ease}.nav-menu.active{right:0;transition:all 1s ease;z-index:1;height:calc(100vh - min(60px, 20vw))}.nav-item{height:20vw;max-height:75px;align-items:center}.nav-links{text-align:center;align-items:center;width:300px;display:table;height:10vw;margin-top:30px}.nav-links:hover{background-color:#fff;color:#242424;border-radius:0}.menu-icon{display:block;position:absolute;top:0;right:0;-webkit-transform:translate(-100%, 30%);transform:translate(-100%, 30%);font-size:1.8rem;cursor:pointer}.fa-times{color:#fff;font-size:2rem;display:flex}.nav-links-mobile{display:block;text-align:center;border-radius:4px;width:80%;text-decoration:none;font-size:50vw;background-color:#242424;color:#fff;transition:all 0.3s ease-out}.nav-links-mobile:hover{background:#fff;color:#242424;transition:250ms}}

.home{background-color:#f2f2f2;height:100vh;display:flex;scroll-snap-align:start}.home .left{flex:0.5 1;overflow:hidden;position:relative}.home .left .img-container{bottom:0;width:40vw;height:40vw;background-color:#7f5463;border-radius:50%;display:flex;align-items:flex-end;justify-content:center;float:right;position:absolute}.home .left .img-container img{width:100%}.home .right{flex:0.5 1;position:relative}.home .right .wrapper{width:100%;height:100%;display:flex;flex-direction:column;padding-left:50px;justify-content:center}.home .right .wrapper h2{font-family:'Lora';font-size:35px;font-weight:normal;color:#7f5463}.home .right .wrapper h1{font-family:'Montserrat';font-weight:lighter;font-size:60px;margin:10px 0}.home .right .wrapper h3{font-family:'Lora';font-weight:normal;font-size:30px}.home .right .wrapper h3 span{color:#7f5463;font-size:inherit}.home .right .wrapper h3 .ityped-cursor{-webkit-animation:blink 1s infinite;animation:blink 1s infinite}@-webkit-keyframes blink{50%{opacity:1}100%{opacity:0}}@keyframes blink{50%{opacity:1}100%{opacity:0}}.home .right .arrow-container{width:25px;-webkit-animation:bounce 1600ms infinite cubic-bezier(0.445, 0.05, 0.55, 0.95);animation:bounce 1600ms infinite cubic-bezier(0.445, 0.05, 0.55, 0.95);cursor:pointer;height:20px;position:absolute;bottom:20px;left:30%}@-webkit-keyframes bounce{50%{-webkit-transform:translateY(-15px);transform:translateY(-15px)}}@keyframes bounce{50%{-webkit-transform:translateY(-15px);transform:translateY(-15px)}}.home .right .arrow-down{height:3px;background:#7f5463;-webkit-transform:rotate(45deg);transform:rotate(45deg);-webkit-transform-origin:0% 0%;transform-origin:0% 0%;border-radius:5px}.home .right .arrow-down:after{content:'';position:absolute;width:100%;height:100%;bottom:0;left:100%;border-radius:5px;background:#7f5463;-webkit-transform:rotate(-90deg);transform:rotate(-90deg);-webkit-transform-origin:0% 100%;transform-origin:0% 100%}

.about{background-color:white;position:relative;display:flex;flex-direction:column;align-items:center}.about .title{display:flex;flex-direction:row;margin-top:65px}.about .title h1{font-family:'Montserrat';font-weight:lighter;font-size:60px;margin:10px 0;color:black;margin-right:15px}.about .title h2{font-family:'Montserrat';font-weight:lighter;font-size:60px;margin:10px 0;color:#7f5463}.about .subtitle h4{font-family:'Lora';font-style:italic;font-weight:bold;font-size:24px;color:#797979}.about .description{max-width:80vw;display:flex;flex-direction:column;align-items:center}.about .description p{margin-top:20px;font-size:20px;font-family:"Lora"}.about .description p .contact,.about .description p a{color:#7f5463;font-weight:bold;cursor:pointer}.about .links{display:flex;flex-direction:row;margin-top:30px;margin-bottom:70px}.about .links .link-wrapper{display:flex;align-items:center}.about .links .link-wrapper a{text-decoration:none}.about .links .link-wrapper a .link{height:8vh;width:8vh;max-height:50px;max-width:50px;margin-right:10px;margin-left:10px;color:#797979}.about .links .link-wrapper a .link:hover{color:#7f5463}.about .links .link-wrapper a .link-smaller{height:7vh;width:7vh;max-height:40px;max-width:40px;margin-right:10px;margin-left:10px;color:#797979}.about .links .link-wrapper a .link-smaller:hover{color:#7f5463}

.resume{background-color:white;position:relative;display:flex;flex-direction:column;align-items:center}.resume .title{display:flex;flex-direction:row;margin-top:10px}.resume .title h1{font-family:"Montserrat";font-weight:lighter;font-size:60px;margin:10px 0;color:black;margin-right:15px}.resume .title h2{font-family:"Montserrat";font-weight:lighter;font-size:60px;margin:10px 0;color:#7f5463}.resume .row{display:flex;width:70vw;max-width:70vw}.resume .row .nav{position:sticky;width:calc(max(190px, 10vw));margin-top:40px}.resume .row .nav .nav-list{text-decoration:none;list-style-type:none;position:sticky;top:120px}.resume .row .nav .nav-list .nav-element{margin-bottom:10px}.resume .row .nav .nav-list .nav-element .nav-links{color:#000;font-family:"Montserrat";text-decoration:none;font-size:20px}.resume .row .nav .nav-list .nav-element .nav-links:hover{color:#7f5463}.resume .row .nav .nav-list .nav-element .nav-links.active{color:#7f5463;margin-left:20px;list-style-type:"-";transition:all 0.3s ease}.resume .row .body{margin-left:30px}.resume .row .body h3{font-family:"Montserrat";font-weight:normal;font-size:30px;margin-top:30px;color:#7f5463}.resume .row .body .item{margin-top:10px;display:flex;flex-direction:row;margin-bottom:10px;border-bottom:1px solid #d6d6d6}.resume .row .body .item .circle{height:40px;width:40px;border-radius:50%;background-color:#7f5463;margin-right:10px;margin-left:10px;align-items:center;justify-content:center;display:flex}.resume .row .body .item .circle .item-icon{width:40px;vertical-align:middle;text-align:center;align-self:center;color:white}.resume .row .body .item .text{display:flex;flex-direction:column;margin-bottom:20px}.resume .row .body .item .text p{font-family:"Lora";font-size:20px;color:#797979;margin-bottom:5px}.resume .row .body .item .text p a{color:#7f5463}.resume .row .body .item .text h4{font-family:"Montserrat";font-weight:normal;font-size:25px}.resume .row .body .item .text h5{font-family:"Lora";font-size:18px;margin-bottom:10px}.resume .row .body .item .text h6{font-family:"Lora";font-size:18px;color:#7f5463}

